    var im5 = "";
    /*
    var topic1 = "หัวข้อ 1 ไม่เกิน 30 ตัว(ไม่ใส่ก็ได้)";
    var topic2 = "หัวข้อ 2 ไม่เกิน 30 ตัว";
    var topic3 = "หัวข้อ 3 ไม่เกิน 30 ตัว";
    var subTopic = "คำอธิบาย ไม่เกิน 30 ตัว";
    var name = "ชื่อ - สกุล";
    var title = "ตำแหน่ง";
    var dept = "กลุ่มสาระฯ";
    var school = "โรงเรียน";
    var sso1 = "สังกัด 1";
    var sso2 = "สังกัด 2";
    var sso3 = "สังกัด 3";*/

    var topic1 = "";
